.main-nav__wrapper {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 342px;
  min-width: 342px;

  @media (min-width: $tablet) {
    width: 700px;
  }

  @media (min-width: $desktop) {
    width: 952px;
  }
}

.mobile-bg {
  @media (max-width: $tablet) {
    position: fixed;
    left: -12px;
    right: 0;
    z-index: 9999;
    width: 100vw;
    height: 100vh;
    min-width: 384px;
    background: $color-white-light;
  }
}

.main-nav--opened {
  @media (max-width: $tablet) {
    margin-bottom: 71px;
  }
}

.main-nav__toggle {
  position: relative;
  z-index: 2;
  display: block;
  margin-right: 30px;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-color: $color-white-light;
  cursor: pointer;

  &:hover {
    background-color: $color-white-darker;
  }

  @media (min-width: $tablet) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  background-color: $color-white-darker;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  left: 9px;
  top: 9px;
  width: 14px;
  height: 1.5px;
  box-shadow: 0 5px 0 0 $color-dark-2, 0 10px 0 0 $color-dark-2;
  background-color: $color-dark-2;
}

.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  left: calc(50% - 6px);
  top: calc(50% - 6px);
  width: 12px;
  height: 12px;
  background: url("../img/main-nav/cross.svg");
}

.site-navigation {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  text-align: center;

  &[data-desktop_nav] {
    @media (max-width: 766px) {
      display: none;
    }
  }

  &[data-mobile_nav] {
    @media (min-width: 766px) {
      display: none;
    }
  }

  @media (min-width: $tablet) {
    flex-direction: row;
    text-align: left;
  }

  @media (min-width: $tablet) {
    min-width: 363px;
  }
}

.site-navigation__item {
  margin: 0 0 32px;

  & a {
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    color: rgba($color: $color-dark-2, $alpha: 0.45);
    transition: color 0.3s ease;

    @media (min-width: $tablet) {
      margin: 0 32px;
      font-weight: 700;
      font-size: $font-size-body;
      line-height: 19px;
      color: rgba($color: $color-white-light, $alpha: 0.45);
      transition: color 0.3s ease;

      &:hover {
        color: $color-white-light;
        transition: color 0.3s ease;
      }
    }
  }

  &--profile a {
    color: rgba($color: $color-dark-2, $alpha: 0.45);
    transition: color 0.3s ease;

    &:hover {
      color: $color-dark-2;
      transition: color 0.3s ease;
    }
  }

  @media (max-width: $tablet) {
    &--active a,
    & a:hover {
      color: $color-dark-2;
      transition: color 0.3s ease;
    }
  }

  @media (min-width: $tablet) {
    margin: 0;

    &--active a {
      color: $color-white-light;
      opacity: 1;
      transition: color 0.3s ease;
    }
  }
}

.button--user {
  font-size: $font-size-caption;
  line-height: 17px;
}

.main-nav--opened .button--user {
  @media (max-width: $tablet) {
    color: $color-white-light;
    background-color: $color-brand;
  }
}

.main-nav--opened .button--account {
  @media (max-width: $tablet) {
    color: $color-dark-2;
    background-color: $color-white-darker;
  }
}

object {
  pointer-events: none;
}

